/* 聯絡我們contact(PC) */
label {
  display: block; }

.contact {
  background-color: #AA06010D;
  /* 為了能讓H2的mt在contact內(被景色能一致)，故要使用flex */
  display: flex;
  flex-direction: column;
  padding-bottom: 92px; }

.contact-container {
  max-width: 792px;
  margin: 0 auto; }
  .contact-container p {
    font-size: 20px;
    font-weight: 600;
    line-height: 30px;
    margin-top: 24px;
    margin-bottom: 18px; }
  .contact-container label {
    margin-bottom: 4px; }
  .contact-container input, .contact-container textarea {
    width: 100%;
    font-family: 'Microsoft JhengHei';
    font-size: 16px;
    border: 1px solid #000000; }
  .contact-container input {
    padding: 12.5px 16px; }
  .contact-container textarea {
    height: 136px;
    padding: 14px 16px;
    box-sizing: border-box;
    /* border: 2px solid #ccc;
        border-radius: 4px;
        background-color: #f8f8f8; */
    resize: none; }

.checkbox {
  display: flex;
  align-items: center; }
  .checkbox label {
    margin-bottom: 0;
    line-height: 24px; }
  .checkbox input {
    margin: 4px;
    width: 22px;
    height: 22px; }

.frm-btn {
  display: block;
  width: 180px;
  height: 54px;
  line-height: 54px;
  text-align: center;
  color: #ffffff;
  background-color: #AA0601; }

/* contact聯絡我們(平板) */
@media (max-width: 768px) {
  .contact {
    font-size: 16px;
    padding-bottom: 56px; }
  .contact-container {
    max-width: 498px; }
    .contact-container p {
      font-size: 16px;
      line-height: 24px;
      margin-top: 1rem;
      margin-bottom: 0.875rem; }
  .frm-btn {
    width: 136px;
    height: 36px;
    line-height: 36px; } }

/* contact聯絡我們(手機) */
@media (max-width: 375px) {
  .contact {
    padding-bottom: 6rem; }
  .contact-container {
    max-width: 327px; }
    .contact-container p {
      font-size: 2rem;
      line-height: 3rem;
      margin-bottom: 1rem; }
    .contact-container label {
      font-size: 2rem;
      line-height: 21px; }
  .checkbox {
    align-items: start; }
    .checkbox input {
      margin-top: 0; }
  .frm-btn {
    font-size: 2rem;
    font-weight: 600; } }

.faq-banner-bg {
  position: relative; }

.faq-banner {
  display: flex;
  align-items: flex-end; }
  .faq-banner h2 {
    position: absolute;
    bottom: 16px;
    display: flex;
    align-items: center; }
    .faq-banner h2 img {
      margin-left: 16px; }

.faq-banner-pad {
  display: none; }

.faq-banner-phone {
  display: none; }

.dropdown {
  position: absolute;
  left: 0;
  top: 100%;
  width: 100%;
  max-height: 0px;
  transition: all 0.8s;
  overflow: hidden;
  z-index: 120; }

.dropdown.active {
  max-height: 200px; }

.dropdown li a {
  display: block;
  background: #AA0601;
  color: #FFFFFF;
  border-top: 1px solid #FFFFFF;
  padding: 16px 0;
  text-decoration: none; }

.dropdown li a:hover {
  background: #8f0400; }

.sec-page h3 {
  font-size: 32px;
  line-height: 63px;
  font-weight: bold;
  margin-top: 88px;
  margin-bottom: 16px; }

.faq {
  margin-bottom: 156px; }

.faq-item {
  border: 1px solid #000000; }

.faq-item-header {
  padding: 22px 38px;
  border-bottom: 1px solid #000000; }
  .faq-item-header a {
    text-decoration: none;
    color: #000000;
    display: flex;
    justify-content: space-between;
    align-items: center; }
  .faq-item-header h4 {
    font-size: 20px;
    line-height: 23px;
    font-weight: bold; }

.faq-item-body {
  background-color: #F2F2F2;
  padding: 22px 38px;
  padding-right: 246px;
  font-size: 16px;
  line-height: 24px; }

.faq-item:not(.active) .up {
  display: none; }

.faq-item.active .down {
  display: none; }

@media (max-width: 768px) {
  .faq-banner-pc {
    display: none; }
  .faq-banner-pad {
    display: flex;
    align-items: center; }
  .faq-banner h2 img {
    width: 48px;
    background: #FFFFFF;
    border-radius: 50%; }
  .sec-page h3 {
    font-size: 28px;
    line-height: 63px;
    margin-top: 40px;
    margin-bottom: 32px; }
  .faq-item-header {
    padding: 16px 24px; }
    .faq-item-header h4 {
      font-size: 16px;
      line-height: 24px; }
  .faq-item-body {
    font-size: 14px;
    line-height: 21px;
    padding: 16px 24px; }
  .faq {
    margin-bottom: 104px; } }

@media (max-width: 375px) {
  .faq {
    margin-bottom: 56px; }
  .faq-banner h2 {
    font-size: 28px;
    bottom: 8px; }
    .faq-banner h2 img {
      width: 24px;
      margin-left: 8px; }
  .sec-page h3 {
    font-size: 20px;
    line-height: 28px;
    margin-top: 24px;
    margin-bottom: 16px; }
  .faq-item-header {
    padding: 16px; }
  .faq-item-body {
    padding: 16px; } }
